.balance {
    font-size: 50px;
    font-weight: 700;
}

.shop {
    height: 81px;
    border-radius: 20px;
    margin-left: 10px;
}

.upgrade .name {
    font-weight: 600;
}

.upgrade .mine {
    color: #909499
}

.click {
    width: 95%;
    margin-top: 70px;
    height: 200px;
}

.scrollShop {
    position: relative;
    overflow: hidden;
}

.scrollShop_in {
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    padding-bottom: 20px;
    margin-bottom: -20px;
    height: 400px;
}

